import {postRequest,putRequest,deleteRequest,getRequest,postKeyValueRequest} from "../utils/request";

export const listStall = (params) => {
    return postRequest("/uiapi/v1/stall/list", params)
}

export const createStall = (params) => {
    return postRequest("/uiapi/v1/stall/add", params)
}

export const updateStall = (params) => {
    return putRequest("/uiapi/v1/stall/update", params)
}

export const deleteStall = (params) => {
    return deleteRequest("/uiapi/v1/stall/delete", params)
}

export const listApply = (params) => {
    return postRequest("/uiapi/v1/apply/list", params)
}

export const createApply = (params) => {
    return postRequest("/uiapi/v1/apply/add", params)
}

export const updateApply = (params) => {
    return putRequest("/uiapi/v1/apply/update", params)
}
