<template>
    <div style="margin-top: 40px">

        <el-table
                :data="listData"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="stallName"
                    label="摊位名称"
                    width="400">
            </el-table-column>
            <el-table-column
                    prop="bazaarName"
                    label="所属市场"
                    width="300">
            </el-table-column>
            <el-table-column
                    prop="status"
                    label="状态"
                    :formatter="formatStatus"
            >
            </el-table-column>
            <el-table-column
                    prop="message"
                    label="审批消息"
            >
            </el-table-column>
            <el-table-column
                    prop="applyTime"
                    label="审批时间"
                    :formatter="formatApplyTime"
            >
            </el-table-column>
        </el-table>
        <el-row :gutter="20" style="margin-top: 25px">
            <el-col :span="4" :offset="18">
                <el-pagination
                        background
                        :layout=pageInfo.layout
                        :total="pageInfo.total">
                </el-pagination>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import {listApply} from "../../../api/stallApi";

    export default {
        name: "userApplyList",
        data() {
            return {
                user: JSON.parse(window.sessionStorage.getItem('user')),
                listData: [],
                queryParam: {
                    "pageNumber":1,
                    "pageSize": 10,
                    "conditions": {
                        "userId": JSON.parse(window.sessionStorage.getItem('user')).userId,
                        "bazaarId": null,
                        "status": null
                    }

                },
                formData: {
                    id: null,
                    bazaarId: null,
                    bazaarName: null,
                    stallId: null,
                    stallName: null,
                    userId: null,
                    userName: null,
                    status: null
                },
                pageInfo: {
                    layout: "prev, pager, next, jumper, total",
                    total: 10
                }
            }
        },
        created() {
            this.listUserApply()
        },
        methods: {
            listUserApply() {
                listApply(this.queryParam).then(res => {
                    this.listData = res.data.first
                    this.pageInfo.total = res.data.second
                })
            },

            formatStatus(row,column) {
                if(row.status === 0) {
                    return "待审批"
                } else if (row.status === 1) {
                    return "成功"
                } else {
                    return "失败"
                }
            },
            formatApplyTime(row,column) {
                if (row.handlerTime == null || row.handlerTime === '') {
                    return '-'
                }
                return row.handlerTime
            }
        }
    }
</script>

